.app {
  height: 100%;
}
.grid-element {
  display: flex;
  height: 170px;
  width: 170px;
  background-color: rgba(0, 0, 0);
  background-size: cover;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  margin-bottom: 10px;
}

.artist-element {
  border-radius: 100px;
  display: flex;
  height: 170px;
  width: 170px;
  background-size: cover;
  align-items: center;
  justify-content: center;
  color: white;
}

.similar-to {
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  color: white;
