}
.top-artist1 {
  margin-right: 30px;
}

.top-artist2 {
  margin-left: 30px;
}

.section-title {
  color: black;
  display: flex;
  font-weight: bold;
  font-size: 15pt;
  margin: 30px;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  justify-content: center;
}

.top-artist {
  text-align: left;
  display: flex;
  align-items: flex-end;
}

.artist-info {
  position: relative;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    sans-serif;
  padding: 20px;
