  margin-bottom: 5px;
}
.artist-tag {
  display: inline-block;
  color: white;
  height: 30px;
  text-align: center;
  margin-right: 10px;
  border: solid white;
  border-width: 0.5px;
  border-radius: 30px;
  cursor: pointer;
  opacity: 0.65;
}

.artist-tag:hover {
  opacity: 1;
  color: white;
}

.tag {
  display: flex;
  align-items: center;
