.fb-profile-block {
  margin: auto;
  position: relative;
  width: 100%;
}
.cover-container {
  width: 100%;
  background: repeating-linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.7),
      rgba(0, 0, 0, 0.7)
    ),
    url("https://visualcocaine.org/public/uploads/large/191512467154mhnqdac6kkbqrpewsmarmfi1uagruo1zg7lo8qxgjyaynoqgiffni5mw2tlfi8jxdpu4nq8p3idqz3hq84lwcmfjc5ibpw0dc1xz.jpg");
  background-size: cover;
}

.fb-profile-block-thumb {
  display: block;
  height: 200px;
  overflow: hidden;
  position: relative;
  text-decoration: none;
}

.profile-img a {
  margin-top: 10px;
  bottom: 15px;
  box-shadow: none;
  display: block;
  left: 15px;
  padding: 1px;
  position: absolute;
  height: 160px;
  width: 160px;
  z-index: 9;
}
.profile-img img {
  margin-top: 10px;
  background-color: #fff;
  border-radius: 2px;
  height: 158px;
  padding: 5px;
  width: 158px;
  border-radius: 100px;
}
.profile-name {
  bottom: 60px;
  left: 200px;
  position: absolute;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
}
.profile-name h2 {
  color: white;
  font-size: 24px;
  font-weight: 490;
  line-height: 30px;
  max-width: 275px;
  position: relative;
  text-transform: uppercase;
}
.fb-profile-block-menu {
  height: 44px;
  position: relative;
  width: 100%;
  overflow: hidden;
}
.block-menu {
  clear: right;
  padding-left: 205px;
}
.block-menu ul {
  margin: 0;
  padding: 0;
}

.block-menu ul li {
  display: inline-block;
}
.block-menu ul li a {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  height: 42px;
  padding-right: 10px;
