.login {
  height: 64.1vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.login-form {
  max-width: 300px;
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
  height: 40px;
}
.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 64.1vh;
}
.login-label {
  margin-bottom: 5px;
  width: 300px;
  text-align: center;
  font-size: 30pt;
  font-weight: 500;
  color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.remember-me {
  color: rgb(0, 83, 153);
  font-weight: 400;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

a{
  text-decoration: none;
  font-size: 11pt;
  font-weight: 420;
  color: rgb(0, 83, 153);
}

.login-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}
